import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

export interface Board {
  action: string;
  data?: any;
}

@Injectable({
  providedIn: 'root'
})
export class BoardService {
  private subject = new Subject<Board>();

  constructor() { }

  getBoard(): Observable<any> {
    return this.subject.asObservable();
  }

  addMove(x: number, y: number) {
    this.subject.next({
      action: 'addMove',
      data: {
        x: x,
        y: y
      }
    });
  }

  reset() {
    this.subject.next({
      action: 'reset'
    });
  }

  retract() {
    this.subject.next({
      action: 'retract'
    });
  }

  disable() {
    this.subject.next({
      action: 'disable'
    });
  }

  enable() {
    this.subject.next({
      action: 'enable'
    });
  }

  normalColor() {
    this.subject.next({
      action: 'normalColor'
    });
  }

  oneColor(color: string) {
    this.subject.next({
      action: 'oneColor',
      data: {
        color: color
      }
    });
  }


}
